import React from 'react';
import classes from './Button.css';

const Button = (props) => (
  <button onClick = {props.clicked} disabled = {props.disabled} className = {[classes.Button, classes[props.btnType]].join(' ')}>
    {props.children}
  </button>
);

export default Button;



